@import url(https://fonts.googleapis.com/css2?family=Oleo+Script:wght@400;700&family=Quicksand:wght@600&family=Roboto+Slab&display=swap);
:root{--color: rgb(32, 48, 54);--background-color: rgb(248, 244, 240);--stroke-color: rgba(0, 0, 0, 0.5);--shadow-color: rgba(12, 8, 4, 0.15);--header-background: #99d2db;--hover-color: #42a5f5;--link-color: #ba68c8;--stripes: repeating-linear-gradient( 90deg, rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1) 3rem, rgba(0, 0, 0, 0.05) 3rem, rgba(0, 0, 0, 0.05) 6rem );--purple-gradient: linear-gradient(220.55deg, #a531dc 0%, #4300b1 100%);--red-gradient: linear-gradient(220.55deg, #ff896d 0%, #d02020 100%);--blue-gradient: linear-gradient(220.55deg, #3793ff 0%, #0017e4 100%);--orange-gradient: linear-gradient(220.55deg, #ffd439 0%, #ff7a00 100%);--teal-gradient: linear-gradient(220.55deg, #7cf7ff 0%, #4b73ff 100%);--gray-gradient: linear-gradient(220.55deg, #eaeaea 0%, #8b8b8b 100%);--black-gradient: linear-gradient(220.55deg, #565656 0%, #181818 100%);--yellow-gradient: linear-gradient(220.55deg, #fff500 0%, #ffb800 100%);--cozy-gradient: linear-gradient(220.55deg, #ff8570 0%, #ff8bf3 100%);--green-gradient: linear-gradient(220.55deg, #00b960 0%, #00552c 100%);--midnight-gradient: linear-gradient(220.55deg, #00e0ee 0%, #a060ee 100%);--fizzy-gradient: linear-gradient(220.55deg, #ff8570 0%, #418cb7 100%);--fizzy-light-gradient: linear-gradient(220.55deg, #89c6ff 0%, #ff8bf3 100%);--vital-ocean: linear-gradient(90deg, #1cb5e0 0%, #000851 100%);--kale-salad: linear-gradient(90deg, #00c9ff 0%, #92fe9d 100%);--disco-club: linear-gradient(90deg, #fc466b 0%, #3f5efb 100%);--shady-lane: linear-gradient(90deg, #3f2b96 0%, #a8c0ff 100%);--retro-wagon: linear-gradient(90deg, #fdbb2d 0%, #22c1c3 100%);--fresco-crush: linear-gradient(90deg, #fdbb2d 0%, #3a1c71 100%);--cucumber-water: linear-gradient(90deg, #e3ffe7 0%, #d9e7ff 100%);--sea-salt: linear-gradient(90deg, #4b6cb7 0%, #182848 100%);--par-four: linear-gradient(90deg, #9ebd13 0%, #008552 100%);--ooey-gooey: linear-gradient(90deg, #0700b8 0%, #00ff88 100%);--bloody-mimosa: linear-gradient(90deg, #daae51 0%, #d53369 100%);--lovely-lily: linear-gradient(90deg, #efd5ff 0%, #515ada 100%);--aqua-spray: linear-gradient(90deg, #00d2ff 0%, #3a47d5 100%);--mello-yellow: linear-gradient(90deg, #f8ff00 0%, #3ad59f 100%);--dusty-cactus: linear-gradient(90deg, #fcff9e 0%, #c67700 100%);--sexy-sandal: linear-gradient(90deg, #fc466b 0%, #fc466b 100%);--radius-xl: 48px;--radius-lg: 32px;--radius-md: 8px;--radius-sm: 4px;--easeInOutQuadratic: cubic-bezier(0.455, 0.03, 0.515, 0.955);--easeOutBack: cubic-bezier(0.34, 1.56, 0.64, 1);--easeOutBack-large: cubic-bezier(0.175, 0.885, 0.405, 1.57)}html{box-sizing:border-box}*,*:before,*:after{box-sizing:inherit;caret-color:transparent}*:focus,*:before:focus,*:after:focus{outline:auto 5px -webkit-focus-ring-color}body{margin:0;font-family:"Roboto Slab",serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;min-height:100vh;margin:0 auto;display:flex;flex-direction:column;color:rgb(32, 48, 54);color:var(--color);box-sizing:border-box}body h1,body h2,body h3,body h4,body h5,body h6{text-transform:capitalize;font-family:"Oleo Script","Oxygen","Ubuntu","Cantarell","Fira Sans","Droid Sans","Helvetica Neue",sans-serif;color:var(--title-color-intense);margin:0;padding:0}body input{text-transform:lowercase;font-family:"Quicksand",sans-serif;font-size:1rem}body h1{color:var(--title-color)}body h2{color:var(--subtitle-color);font-size:clamp(2.5rem,1vw + 1.5rem,6rem)}body ol,body ul{font-size:clamp(.925rem,3vw,1.25rem)}body #root{width:100%;overflow:hidden}code{font-family:source-code-pro,Menlo,Monaco,Consolas,"Courier New",monospace}
